import { Component, OnInit } from '@angular/core';
import { BrandService } from '../../../../../auth/_services/brand.service';
import { QuickSidebarComponent } from '../../../../layouts/quick-sidebar/quick-sidebar.component';
import { SideBarToggleComponent } from '../../../../layouts/side-bar-toggle/side-bar-toggle.component';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { DataService } from '../../../../../_services/data.service';
// import { NotificationsService } from 'angular4-notify';
import { ToastrService } from 'ngx-toastr';
import { Router, ActivatedRoute } from "@angular/router";
import { ScriptLoaderService } from '../../../../../_services/script-loader.service';

@Component({
    selector: 'app-brand-list',
    templateUrl: "brand-list.component.html",
    styles: []
})
export class BrandListComponent implements OnInit {
    form: FormGroup;
    private formSumitAttempt: boolean;
    constructor(private router: Router, private imgBaseUrl: ScriptLoaderService, private toastr: ToastrService, private _brandService: BrandService, private _dataService: DataService, private formBuilder: FormBuilder) { }
    public brandData;
    public searchValue = '';
    public brandDrpdown: any;
    public dropDownValue = '';
    public selectedStatus = '';
    public dataSaved = false;
    public saveMsg = '';
    brandImgUrl: any;
    p: number = 1;
    public ImgUrl: any;
    ngOnInit() {


        /*user permission*/
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        let permission = currentUser.success.permission;
        if (permission.product_brand != 1) {
            this.toastr.error('You have not permission to visit this page.', 'Error Message');
            this.router.navigate(['/']);
        }
        /*user permission*/

        // this.notificationsService.addInfo('Brand Added Successfully');
        this._dataService.setloaderState(false);
        this._dataService.setactiveMenu('brand');
        this.ImgUrl = this.imgBaseUrl._imgBaseUrl;
        localStorage.setItem('section', 'brand');
        this._brandService.getAll().subscribe(
            data => {
                this.brandData = data.success;

            }

        );


        this.form = this.formBuilder.group({
            name: [null, Validators.required],
            status: [null, Validators.required]
        });
    }



    isFieldValid(field: string) {
        return (
            (!this.form.get(field).valid && this.form.get(field).touched) ||
            (this.form.get(field).untouched && this.formSumitAttempt)
        );
    }

    displayFieldCss(field: string) {
        return {
            'has-error': this.isFieldValid(field),
            'has-feedback': this.isFieldValid(field)
        };
    }


    handleFileInput(files: FileList) {
        let file = files.item(0);
        var reader = new FileReader();
        reader.onload = (event: any) => {
            this.brandImgUrl = event.target.result;
        }
        reader.readAsDataURL(file);
    }

    onSubmit() {
        let input: any;
        this.formSumitAttempt = true;

        if (this.form.valid && this.brandImgUrl) {
            let that = this;
            input = { form_data: this.form.value }
            input.form_data.brand_img = this.brandImgUrl;
            this._brandService.getByName(input.form_data.name).subscribe(
                searchData => {
                    if (searchData.success) {
                        this.toastr.error('Brand Already Exist', 'Error Message');
                    } else {
                        this._brandService.createBrand(input).subscribe(
                            data => {
                                this.brandData = data.data;
                                this.toastr.success('Brand Added Successfully', 'Success Message');
                                this.reset();
                            }
                        );
                    }
                }
            );

        }
    }

    hideMsg() {
        setTimeout(function() {
            this.dataSaved = false;
        }.bind(this), 4000);
    }

    reset() {
        if (this.selectedStatus != '') {
            this.form.reset();
            this.selectedStatus = '';
            this.formSumitAttempt = false;
        }
    }



    public brand_delete(del_id) {


        console.log(del_id);
        let value = confirm('Are u want to delete this brand?');
        if (value) {
            this._brandService.brandDelete(del_id).subscribe(
                data => {
                    this.brandData = data.data;
                }
            );

        }
    }


    public changeBrandStatus(brand: any) {

        console.log(brand);

        if (brand.status == 'Active') {
            brand.status = 'Inactive';
            this._brandService.updateStatus(brand.id).subscribe(

                data => {

                }
            );
        }
        else {
            brand.status = 'Active';
            this._brandService.updateStatus(brand.id).subscribe(

                data => {

                }
            );
        }
    }

    public search() {
        this._brandService.searching({ search_value: this.searchValue, dropdown_value: this.dropDownValue }).subscribe(
            data => {
                this.brandData = data.success;
            }
        );
    }

}
